<template>
  <el-card>
    <div slot="header">
      <span>开服管理</span>
      <el-button style="float: right; padding: 3px 0" type="text" @click="handleOpenAdd">新增</el-button>
    </div>
    <el-table :data="aTableData" tooltip-effect="dark" style="width: 100%">
      <el-table-column prop="serverId" label="服务器ID" min-width="100"></el-table-column>
      <el-table-column prop="name" label="名称" min-width="100" show-overflow-tooltip></el-table-column>
      <el-table-column prop="start" label="开服时间" min-width="100" show-overflow-tooltip></el-table-column>
      <el-table-column prop="display" label="列表显示" min-width="100" show-overflow-tooltip></el-table-column>
      <el-table-column prop="whiteList" label="白名单" min-width="100" show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{scope.row.whiteList.length?scope.row.whiteList.join(','):''}}</span>
        </template>
      </el-table-column>
      <el-table-column label="服务器状态" min-width="100" show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{Number(scope.row.serverStatus)==1?'正常':Number(scope.row.serverStatus)==2?'爆满':'维护'}}</span>
        </template>
      </el-table-column>
      <el-table-column label="是否新服" min-width="100" show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{Number(scope.row.isNew)?'是':'否'}}</span>
        </template>
      </el-table-column>
      <el-table-column label="是否推荐" min-width="100" show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{Number(scope.row.isRecommend)?'是':'否'}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="platform" label="平台" min-width="100" show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{returnPlatform(scope.row.platform)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="host" label="服务器地址" min-width="100" show-overflow-tooltip></el-table-column>
      <el-table-column prop="uid" label="角色ID" min-width="100" show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{scope.row.uid?scope.row.uid.length?scope.row.uid.join(','):'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="100">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="handleClickUpdate(scope.row)"
            type="text"
            size="small"
          >编辑</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="数据新增" :visible.sync="bDialogAdd">
      <el-form :model="objFormAdd" :rules="rules" ref="objFormAdd" label-width="170px">
        <el-form-item label="服务器ID" prop="serverId">
          <el-input v-model="objFormAdd.serverId" placeholder="请输入服务器ID"></el-input>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="objFormAdd.name" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item label="开服时间" prop="start">
          <el-date-picker
            v-model="objFormAdd.start"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetime"
            placeholder="选择开服时间"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="是否在服务器列表显示" prop="display">
          <el-radio-group v-model="objFormAdd.display">
            <el-radio label="1">是</el-radio>
            <el-radio label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="白名单">
          <el-input
            v-model="objFormAdd.whiteList"
            placeholder="请输入白名单，并用英文字符逗号【,】隔开！"
            type="textarea"
          ></el-input>
        </el-form-item>
        <el-form-item label="服务器状态" prop="serverStatus">
          <el-radio-group v-model="objFormAdd.serverStatus">
            <el-radio label="1">爆满</el-radio>
            <el-radio label="2">正常</el-radio>
            <el-radio label="3">维护</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="是否新服" prop="isNew">
          <el-radio-group v-model="objFormAdd.isNew">
            <el-radio label="1">是</el-radio>
            <el-radio label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="是否推荐" prop="isRecommend">
          <el-radio-group v-model="objFormAdd.isRecommend">
            <el-radio label="1">是</el-radio>
            <el-radio label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="平台" prop="platform">
          <el-select v-model="objFormAdd.platform" placeholder="请选择平台">
            <el-option
              v-for="(ele,idx) in aPlatform"
              :label="ele.name"
              :value="ele.id+''"
              :key="idx"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="服务器地址">
          <el-input v-model="objFormAdd.host" placeholder="请输入服务器地址"></el-input>
        </el-form-item>
        <el-form-item label="角色ID">
          <el-input v-model="objFormAdd.uid" placeholder="请输入角色ID，并用英文字符逗号【,】隔开！" type="textarea"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSubmitAdd('objFormAdd')">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog title="数据修改" :visible.sync="bDialogUpdate">
      <el-form :model="objFormUpdate" :rules="rules" ref="objFormUpdate" label-width="170px">
        <el-form-item label="服务器ID" prop="serverId">
          <el-input v-model="objFormUpdate.serverId" placeholder="请输入服务器ID"></el-input>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="objFormUpdate.name" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item label="开服时间" prop="start">
          <el-date-picker
            v-model="objFormUpdate.start"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetime"
            placeholder="选择开服时间"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="是否在服务器列表显示" prop="display">
          <el-radio-group v-model="objFormUpdate.display">
            <el-radio label="1">是</el-radio>
            <el-radio label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="白名单">
          <el-input
            v-model="objFormUpdate.whiteList"
            placeholder="请输入白名单，并用英文字符逗号【,】隔开！"
            type="textarea"
          ></el-input>
        </el-form-item>
        <el-form-item label="服务器状态" prop="serverStatus">
          <el-radio-group v-model="objFormUpdate.serverStatus">
            <el-radio label="1">爆满</el-radio>
            <el-radio label="2">正常</el-radio>
            <el-radio label="3">维护</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="是否新服" prop="isNew">
          <el-radio-group v-model="objFormUpdate.isNew">
            <el-radio label="1">是</el-radio>
            <el-radio label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="是否推荐" prop="isRecommend">
          <el-radio-group v-model="objFormUpdate.isRecommend">
            <el-radio label="1">是</el-radio>
            <el-radio label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="平台" prop="platform">
          <el-select v-model="objFormUpdate.platform" placeholder="请选择平台">
            <el-option
              v-for="(ele,idx) in aPlatform"
              :label="ele.name"
              :value="ele.id+''"
              :key="idx"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="服务器地址">
          <el-input v-model="objFormUpdate.host" placeholder="请输入服务器地址"></el-input>
        </el-form-item>
        <el-form-item label="角色ID">
          <el-input
            v-model="objFormUpdate.uid"
            placeholder="请输入角色ID，并用英文字符逗号【,】隔开！"
            type="textarea"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSubmitUpdate('objFormUpdate')">修改</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  name: "server",
  data() {
    return {
      bDialogUpdate: false,
      bDialogAdd: false,
      objFormUpdate: {},
      objFormAdd: {},
      aPlatform: [],
      rules: {
        serverId: [
          {
            required: true,
            message: "不能为空！",
            trigger: "blur"
          }
        ],
        name: [
          {
            required: true,
            message: "不能为空！",
            trigger: "blur"
          }
        ],
        start: [
          {
            required: true,
            message: "请选择时间",
            trigger: "blur"
          }
        ],
        display: [
          {
            required: true,
            message: "不能为空！",
            trigger: "change"
          }
        ],
        whiteList: [
          {
            required: true,
            message: "不能为空！",
            trigger: "blur"
          }
        ],
        serverStatus: [
          {
            required: true,
            message: "不能为空！",
            trigger: "change"
          }
        ],
        isNew: [
          {
            required: true,
            message: "不能为空！",
            trigger: "change"
          }
        ],
        isRecommend: [
          {
            required: true,
            message: "不能为空！",
            trigger: "change"
          }
        ],
        platform: [
          {
            required: true,
            message: "不能为空！",
            trigger: "change"
          }
        ],
        host: [
          {
            required: true,
            message: "不能为空！",
            trigger: "blur"
          }
        ],
        uid: [
          {
            required: true,
            message: "不能为空！",
            trigger: "blur"
          }
        ]
      },
      aTableData: []
    };
  },
  methods: {
    returnPlatform(key) {
      let res = "";
      this.aPlatform.forEach(item => {
        if (item.id + "" == key) {
          res = item.name;
        }
      });
      return res;
    },
    handleOpenAdd() {
      this.bDialogAdd = true;
      this.objFormAdd = {
        uid: "",
        whiteList: "",
        host: ""
      };
    },
    handleClickUpdate(row) {
      let objCopy = JSON.parse(JSON.stringify(row));
      let whiteList = "";
      let uid = "";
      if (objCopy.whiteList.length) {
        whiteList = objCopy.whiteList.join(",");
      }
      if (objCopy.uid) {
        if (objCopy.uid.length) {
          uid = objCopy.uid.join(",");
        }
      }
      objCopy = {
        ...objCopy,
        display: objCopy.display + "",
        serverStatus: objCopy.serverStatus + "",
        isNew: objCopy.isNew + "",
        isRecommend: objCopy.isRecommend + "",
        platform: objCopy.platform + "",
        whiteList: whiteList,
        uid: uid
      };
      this.objFormUpdate = objCopy;
      this.bDialogUpdate = true;
    },
    handleSubmitUpdate(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = JSON.parse(JSON.stringify(this.objFormUpdate));
          if (params.whiteList == "") {
            params.whiteList = [];
          } else if (params.whiteList.indexOf(",") == -1) {
            params.whiteList = [params.whiteList];
          } else {
            params.whiteList = params.whiteList.split(",");
          }
          if (params.uid == "") {
            params.uid = [];
          } else if (params.uid.indexOf(",") == -1) {
            params.uid = [params.uid];
          } else {
            params.uid = params.uid.split(",");
          }
          this.$fetch.post(API.server.update_server, params).then(response => {
            let res = response.data;
            if (res.status == 200) {
              this.bDialogUpdate = false;
              this.fetchList();
            } else {
              this.$message.error(res.desc);
            }
          });
        } else {
          return false;
        }
      });
    },
    handleSubmitAdd(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = JSON.parse(JSON.stringify(this.objFormAdd));
          if (params.whiteList == "") {
            params.whiteList = [];
          } else if (params.whiteList.indexOf(",") == -1) {
            params.whiteList = [params.whiteList];
          } else {
            params.whiteList = params.whiteList.split(",");
          }
          if (params.uid == "") {
            params.uid = [];
          } else if (params.uid.indexOf(",") == -1) {
            params.uid = [params.uid];
          } else {
            params.uid = params.uid.split(",");
          }
          this.$fetch.post(API.server.create_server, params).then(response => {
            let res = response.data;
            if (res.status == 200) {
              this.bDialogAdd = false;
              this.fetchList();
            } else {
              this.$message.error(res.desc);
            }
          });
        } else {
          return false;
        }
      });
    },
    fetchList() {
      this.$fetch.post(API.server.server_detail_list).then(response => {
        let res = response.data;
        if (res.status == 200) {
          this.aTableData = res.data;
        } else {
          this.$message.error(res.desc);
        }
      });
    },
    fetchPlatformList() {
      this.$fetch.post(API.coupon.platform_list).then(response => {
        let res = response.data;
        if (res.status == 200) {
          this.aPlatform = res.data;
        } else {
          this.$message.error(res.desc);
        }
      });
    }
  },
  mounted() {
    this.fetchPlatformList();
    this.fetchList();
  }
};
</script>

<style lang="stylus" scoped></style>
